import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { ModalController } from "@ionic/angular";
import { NavParams } from '@ionic/angular';
import $ from 'jquery';
import { LoaderService } from 'src/app/services/loader.service';



@Component({
  selector: 'app-edit-token-type-modal',
  templateUrl: './edit-token-type-modal.component.html',
  styleUrls: ['./edit-token-type-modal.component.scss'],
})
export class EditTokenTypeModalComponent implements OnInit {
  editTokenTypeForm:FormGroup;
  TokenTypeData;
  errMsg;
  @Input() _id: any;
  getId = this.navParams.get('_id');
  valuetokentypeName = true;
  valuetokentypeDesc = true;
  line_value_name="none";
  showBtn;
  line_value_desc = "none";
  tokentypeRight:boolean=false;
  descRight:boolean=false;


  constructor(private fb:FormBuilder, private router: Router, private main_services: MainServicesService, 
    private route: ActivatedRoute, public navParams: NavParams,
     private modalController: ModalController , private loader:LoaderService) { 
      this.editTokenTypeForm = this.fb.group({
        name:['',Validators.required],  		
        priority : ['',Validators.required],
        description:['',Validators.required],
      });
    }

  ngOnInit() {}
   
  editTokenTypeName(){
    this.showBtn = true;
    this.line_value_name = "line";
    this.valuetokentypeName = false;
  }
  InsertData(){
    $('.name-edit-pencil-icon').hide();
    this.tokentypeRight=true;
  }

  editTokenTypeDesc(){
    this.showBtn = true;
    this.line_value_desc = "line";
    this.valuetokentypeDesc = false;
  }
  InsertDesc(){
    $('.desc-edit-pencil-icon').hide();
    this.descRight=true;
  }
  ionViewWillEnter() {
    this.main_services.editTokenType(this.getId).subscribe((result)=>{
      //console.log(result)
      this.TokenTypeData=result['tokenType'];
      this.editTokenTypeForm = this.fb.group({
        name:[this.TokenTypeData.name],  		
        priority : [this.TokenTypeData.priority],
        description:[this.TokenTypeData.description],
      });
  
    })
    
    }

    updateTokenType(value){
      //console.log(value);
      this.main_services.updateTokenType(this.getId,value).subscribe((result)=>{
      this.modalController.dismiss(result, 'success');
      },
      (error)=>
      {
        console.log(error);
        this.errMsg = error['error']['error']['name'];
        if(this.errMsg){
          $('#errors').fadeIn(1000);
        }
        
      })
    }
    dismiss() {
      this.modalController.dismiss({
        'dismissed': true
      });
    }

}
