import { Component, OnInit } from '@angular/core';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import { AlertController, ModalController } from "@ionic/angular";
import { AddTokenTypeModalComponent } from '../add-token-type-modal/add-token-type-modal.component';
import { EditTokenTypeModalComponent } from "../edit-token-type-modal/edit-token-type-modal.component";
import $ from 'jquery';
import { ToastrService } from 'ngx-toastr';
import { LoaderService } from 'src/app/services/loader.service';

@Component({
  selector: 'app-token-types-list',
  templateUrl: './token-types-list.page.html',
  styleUrls: ['./token-types-list.page.scss'],
})
export class TokenTypesListPage implements OnInit {
  tokenTypesList = [];
  searchText;
  sucmsg
  errmsg;
  pageCounts=Array();  // Will Push total no. of pages in this array
  pageCount:number;    // Total no. of Pages for TokenType List (data from API)
  page:number;         // Current Page (data from API)
  leftSize: number;
  rightSize: number;
  showMenu: boolean = false;
 
  constructor(private main_services: MainServicesService, private router: Router, private modalController: ModalController,
    private alertCtrl: AlertController, private toastr: ToastrService, private loader:LoaderService) { }

  ngOnInit() { }

  manuToggle() {
    this.showMenu = !this.showMenu;
    if (this.showMenu == false) {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 3;
        this.rightSize = 9;
      }
      else {
        this.leftSize = 2;
        this.rightSize = 10;
      }
      $('#firstdiv').removeClass("small-aside");
      $('#seconddiv').removeClass("big-right-side");
    }
    else {
      if ($(window).width() < 992) {
        this.rightSize = 12;
      }
      else if ($(window).width() < 1200) {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      else {
        this.leftSize = 1;
        this.rightSize = 11;
      }
      $('#firstdiv').addClass("small-aside");
      $('#seconddiv').addClass("big-right-side");
    }
  }


  hide_mobile_menu() {
    $('#close-show-991').click(function () {
      $('#firstdiv').removeClass("small-aside");
    })
  }

  showMenuMob = true;
  mobileManuToggle() {
    $('#firstdiv').addClass("small-aside");
    // this.showMenuMob = !this.showMenuMob;
  }

  ionViewWillEnter() {
    if ($(window).width() < 992) {
      this.rightSize = 12;
    }
    else if ($(window).width() < 1367) {
      this.leftSize = 3;
      this.rightSize = 9;
    }
    else {
      this.leftSize = 2;
      this.rightSize = 10;
    }

    this.pageCounts= [];
    this.tokenTypesList= [];
    this.main_services.getTokentypesList().subscribe(
      (result) => {
        this.pageCount  = result['pageCount'];
        this.page       = result['page'];
        for(let i=1; i<=result['pageCount']; i++)
        {
          this.pageCounts.push(i);
        }      
        this.tokenTypesList = result['tokenTypes'];
    })
  }

  // Function to switch b/w pages
  paginationData(pageNo) {
    this.tokenTypesList = [];
    this.main_services.tokenTypePagination(pageNo).subscribe(
      (data) => {
        this.page= data['page'];
        this.tokenTypesList = data['tokenTypes'];
      },
      (error) => {
        console.log(error);
      }
    )
  }

  async openModal() {
    const modal = await this.modalController.create({
      component: AddTokenTypeModalComponent,
      cssClass: 'my-token-type-custom-class'
    });
    // return await modal.present();
    await modal.present();


    const { data: result, role } = await modal.onWillDismiss();
    //  const {data:error } = await modal.onWillDismiss();

    this.sucmsg = result['message'];


    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }
  }

  async openEditModal(list) {
    const modal = await this.modalController.create({
      component: EditTokenTypeModalComponent,
      componentProps: {
        _id: list._id,
      },

      cssClass: 'my-edit-token-type-class'
    });


    await modal.present();

    const { data: result, role } = await modal.onWillDismiss();
    this.sucmsg = result['message'];

    if (result['success'] == true) {
      this.toastr.success(this.sucmsg);
      this.ionViewWillEnter();
    }


  }

  async presentAlert(id) {
    const alert = await this.alertCtrl.create({
      cssClass: 'my-alert-class',
      subHeader: 'Do you really want to delete it ?',
      buttons: [{
        text: 'Cancel'
      }, {
        text: 'yes',
        handler: () => {
          this.delete_tokentype(id)
        }
      }]
    });

    await alert.present();
  }


  delete_tokentype(id) {
    this.main_services.deleteTokenType(id).subscribe(
      (data) => {
        this.sucmsg = data['message'];
        this.toastr.success(this.sucmsg);
        this.ionViewWillEnter();
      }, (error) => {
        console.log(error);
      }
    )
  }
}
