import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators} from '@angular/forms';
import { MainServicesService } from './../../../services/main-services.service';
import { Router } from '@angular/router';
import $ from 'jquery';
import { AlertController, ModalController } from "@ionic/angular";

@Component({
  selector: 'app-add-token-type-modal',
  templateUrl: './add-token-type-modal.component.html',
  styleUrls: ['./add-token-type-modal.component.scss'],
})
export class AddTokenTypeModalComponent implements OnInit {
  modelo = "Hello this is my lonnggggggggggggggggg text"


  addTokenTypeForm:FormGroup;  	


  constructor(private fb:FormBuilder, private router: Router, private main_services: MainServicesService, private modalController: ModalController, private alertCtrl: AlertController) {
    this.addTokenTypeForm = this.fb.group({
  		name:['',Validators.required],  		
  		priority : ['',Validators.required],
  		description:['',Validators.required],
      
  	});
   }
   deptName:string;
   tokenTypePriority:string;
   tokenTypeDesc:string;
   errMsg;
   
  ngOnInit() {
    var textarea = document.querySelector('textarea');

    textarea.addEventListener('keydown', autosize);
             
function autosize(){
  var el = this;
  setTimeout(function(){
    // el.style.cssText = 'height:auto; padding:0';
    // for box-sizing other than "content-box" use:
    // el.style.cssText = '-moz-box-sizing:content-box';
    el.style.cssText = 'height:' + el.scrollHeight + 'px';
  },1);
}
  }
  
  dept_name(e){
    this.deptName = e.target.value
  }
  dept_priority(e){
    this.tokenTypePriority = e.target.value
  }

  dept_description(e){
    this.tokenTypeDesc = e.target.value
  }
  // submit(value){
  //   //console.log(value);
  //   this.main_services.addTokenType(value).subscribe((result)=>{
  //     console.log(result);
  //   })
  //   this.router.navigate(['token-types-list']);

  // }
  
  submit()
  {
   let addTokenType = {
     name : this.deptName,
     description : this.tokenTypeDesc
   }
    
    this.main_services.addTokenType(addTokenType).subscribe((result)=>{
      
      this.modalController.dismiss(result, 'success');

    },
    (error)=>{
      console.log(error);
      this.errMsg = error['error']['error']['name'];
      if(this.errMsg){
        $('#errors').fadeIn(1000);
      }
    })
  
   }
   dismiss() {
    this.modalController.dismiss({
      'dismissed': true
    });
  }



  }
